.recent .text {
  padding: 30px;
}
.recent .category span {
  padding: 4px 15px;
  border-radius: 2px;
  font-weight: 600;
  font-size: 13px;
  display: inline-block;
}
.recent .category i {
  font-size: 20px;
  color: #bec7d8;
}
.recent p {
  color: #72809d;
}
.recent .button {
  border-top: 1px solid #e4e8f3;
  padding: 10px 30px;
}
.content {
  display: inline-block;
  width: calc(95% / 3);
  text-align: center;
  margin: 0.5rem;
  margin-top: 20px;
}

img {
  max-width: 100%;
  height: auto;
}
